.page__title {
	clear: both;

	@include sm {
		padding-bottom: 20px; }

	&--bg {
		background-color: #f2ad9a;
		float: left;
		height: 59px;
		width: 15.7%;

		@include md {
			width: 0; }

		@include sm {
			height: 50px; } }

	&__center {
		width: 67.1%;
		margin-left: 0.75%;
		margin-right: 0.75%;
		padding-top: 12px;
		padding-left: 19px;
		background-color: #e8603c;

		@include sm {
			padding-top: 8px; }

		@include md {
			width: 100%;
			margin: 0 auto; } }

	&__text {
		font-family: "Bebas Bold", sans-serif;
		font-size: 30px;
		color: #fff;
		letter-spacing: 0.5px; } }
